<template>
  <div id="app">
    <h1>在线翻译</h1>
    <h5 class="text-muted">简单 | 易用 |便捷 </h5>
    <TranslateForm v-on:formClick="translateText"></TranslateForm>
    <TranslateOutPut v-text="translatedText"></TranslateOutPut>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutPut from './components/TranslateOutPut'

export default {
  name: 'App',
  data: function(){
    return {
      translatedText:""
    }
  },
  components: {
    TranslateOutPut,
    TranslateForm
  },
  methods:{
    translateText:function (text,language) {
      //alert(text)
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20190423T113744Z.072b412b4d3cd258.1c833604b234cf763719221db77c85738003485f&lang='+language+'&text='+text)
        .then((response)=>{
          //console.log(response.body.text[0]);
          this.translatedText = response.body.text[0];
        })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



